
// Body
$body-bg: #f5f8fa;

// Borders
$laravel-border-color: darken($body-bg, 10%);
$list-group-border: $laravel-border-color;
$navbar-default-border: $laravel-border-color;
$panel-default-border: $laravel-border-color;
$panel-inner-border: $laravel-border-color;

// Brands
$brand-primary: #3097D1;
$brand-info: #8eb4cb;
$brand-success: #2ab27b;
$brand-warning: #cbb956;
$brand-danger: #bf5329;

// Typography
$font-family-sans-serif: "Raleway", sans-serif;
$font-size-base: 14px;
$line-height-base: 1.6;
$text-color: #636b6f;

// Navbar
$navbar-default-bg: #fff;

// Buttons
$btn-default-color: $text-color;

// Inputs
$input-border: lighten($text-color, 40%);
$input-border-focus: lighten($brand-primary, 25%);
$input-color-placeholder: lighten($text-color, 30%);

// Panels
$panel-default-heading-bg: #fff;

// App specific

$opacity: 0.2;
$quality-legendary: #EECA2A;
$quality-legendary-border: darken($quality-legendary, 10%);

$quality-artifact: #A02EF7;
$quality-artifact-border: darken($quality-artifact, 10%);

$quality-arcane: #3A92FF;
$quality-arcane-border: darken($quality-arcane, 10%);

$quality-magic: #2DC50E;
$quality-magic-border: darken($quality-magic, 10%);

$quality-normal: white;
$quality-normal-border: darken($quality-normal, 10%);

$quality-trash: white;
$quality-trash-border: darken($quality-trash, 10%);


